@import url("https://fonts.googleapis.com/css2?family=Montserrat:wght@300;400&display=swap");
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: "Montserrat", sans-serif;
}
nav {
  position: absolute;
  display: flex;
  padding: 3rem 10rem;
  justify-content: space-between;
  width: 100%;
  color: white;
}
.container {
  height: 100vh;
}
.container img {
  position: absolute;
  width: 100%;
  height: 110vh;
  z-index: -1;
  overflow: hidden;
}
.main-title {
  position: absolute;
  top: 40%;
  left: 50%;
  font-size: 6rem;
  transform: translate(-50%, -30%);
  font-weight: 300;
  color: white;
}
.content {
  width: 100%;
  background-color: #242a36;
  min-height: 100vh;
  z-index: 2;
  position: absolute;
}
.content-images {
  display: flex;
  justify-content: space-around;
  align-items: center;
  min-height: 60vh;
  text-align: center;
}
.text {
  font-size: 1rem;
  margin: auto;
  text-decoration: none; 
  transition: ease all 0.3s;

}
.text:hover{
  color: rgba(255, 255, 255, 0.5);
}
a{
  color: white;
}
::-webkit-scrollbar {
  background-color: #1b1b1b;
}
::-webkit-scrollbar-thumb {
  background-color: #6a859e;
}
.home {
  height: 90vh;
  position: absolute;
  width: 100%;
  background-color: #181a1f;
  color: black;
  display: flex;
  justify-content: center;
  align-items: center;
}
.home-content{
  display: flex;
  height: 10vh;
  position: absolute;
  width: 50%;
  justify-content: space-between;
}
svg,
.circle {
  stroke-dasharray: 1000;
  
}
@media (max-width:400px){
.main-title{
top: 45%;
}
.text{
  font-size: 90%;
}
#faceit{
  margin-right: 20px;
}
#anime{
  margin-left: 20px;
}
nav{
  justify-content: center;
}
}